import React from 'react';
import './IphoneMockUp.css';
import iphoneblack from './images/iphone-black.png';

const IphoneMockUp = (props) => (
    <img className="iphone" src={iphoneblack} alt="iphone mockup"/>
);

export default IphoneMockUp


